body {
  margin: 0;
}

.stars {
  display: flex;
  flex-direction: row;
}

.stars__star {
  width: 15px;
  height: 15px;
  margin-right: 4px;
  background: url(./images/star-active.svg) no-repeat center;
}

.stars--0 .stars__star:nth-child(n + 1),
.stars--1 .stars__star:nth-child(n + 2),
.stars--2 .stars__star:nth-child(n + 3),
.stars--3 .stars__star:nth-child(n + 4),
.stars--4 .stars__star:nth-child(n + 5) {
  background: url(./images/star.svg) no-repeat center;
}
